/**
* 
*    id
* @param {String} businessDate 
* @param {String} userId id
* @param {String} groupId id
*/
export default function getBusinessInfo() {
    if (window.top && typeof window.top.GETBUSINESSINFO === 'function') {
        return window.top.GETBUSINESSINFO();
    } else {
        return null;
    }
}